<template>
<!-- 
better-scroll -->

<div class="wrapper" ref="wrapper">
<div class="content">
<slot></slot> 
</div>   
</div>  
</template>

<script>
import bscroll from "better-scroll"
export default {
    data:function(){
        return{
            scroll:null,
            
        }
      },
        props:{
          probetype:{
            type:Number,
            default:0
          }
        },
      mounted:function(){
     //
     //this.scroll=new bscroll(document.querySelector(".wrap"),
     this.scroll=new bscroll(this.$refs.wrapper,
     //refthis.$refs.wrapper
     //refthis.$refs.wrapper
     {
       //probeType:2  // ,
       //3,01,
       //0
       probeType:this.probetype,   
       click:true,
       //
       //clicktrue
       pullUpLoad:true,
       //

     });
     //
     this.scroll.on("scroll",(position)=>{
          // console.log(position);
          //position,,
          this.$emit("scrolling",position)
       })
     //
     this.scroll.on("pullingUp",()=>{
    // console.log("");
      this.$emit("pullupload")
    //setTimeout(()=>{
    //this.scroll.finishPullUp()
    //},2000)
     //
      //pullingup
    })

   },
   methods:{
      //
      backTo:function(x,y,time=300){
      //es6
       this.scroll.scrollTo(x,y,time)
        //betterscrollscrollTo3
        //
      },
      pullupend:function(){
        setTimeout(()=>{
         this.scroll.finishPullUp()
    },2000)
      },
      refresh:function(fun){
        this.scroll.refresh()
        //better-scrollrefresh
        //
        //
        //
        fun&&fun()
      },
      gety:function(){
        return this.scroll.y;
      }
   },
}
</script>


<style  scoped>
/* scoped */
.wrapper{
  position: absolute;
  top: 40px;
  bottom: 57.2px;
  right: 0;
  left: 0;
   overflow: hidden;
}
/* height:570px; */
</style>

 