import axios from "axios"

export function req(config){
   const instance=axios.create({
       //
       //baseURL:"http://wthrcdn.etouch.cn",//weather
       //baseURL:"https://autumnfish.cn",//music
        // baseURL:"http://127.0.0.1:3000",   
        //
        //
       
      
     
       timeout:6000,
        // method:"post"
   });

   //axios
   instance.interceptors.request.use((config)=>{
       //console.log(config);

       //configconfig
       return config

   },
   //
   (err)=>{
      console.log(err)
   });

   //axios
   instance.interceptors.response.use(
   (rel)=>{
       //console.log(rel);
       //reldata
       //console.log(rel.data);

      //
      //return rel;
      return rel.data;

   },
   (err)=>{
       console.log(err);
   }
   );


   return instance(config);
   //instancepromise
   //instance promise
}
